:root {
  --background: #0c1021;
  --yellow: #fbde2d;
  --blue: #8da6ce;
  --orange: #ff6400;
  --white: white;
}

html, body {
  background-color: var(--background);
  color: var(--white);
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: JetBrains Mono, monospace;
  font-size: 16px;
}

.nav-list {
  cursor: pointer;
  justify-content: end;
  gap: 50px;
  margin: 0;
  padding: 16px 20px;
  list-style-type: none;
  display: flex;
}

a {
  color: var(--orange);
  text-decoration: none;
}

a:hover {
  color: var(--yellow);
  text-decoration: underline;
}

.main {
  padding: 0 20px;
}

.promptDiv {
  display: flex;
}

.prompt {
  color: var(--yellow);
  margin-right: 10px;
}

.prompt-input {
  caret-color: var(--yellow);
  caret-shape: block;
  color: var(--white);
  width: 100%;
  cursor: text;
  background-color: #0000;
  border: none;
  flex: 1;
  font-family: JetBrains Mono, monospace;
  font-size: 16px;
}

.prompt-input:focus {
  outline: none;
}

